
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IManufacturerForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import ManufacturerForm from '@/views/catalog/manufacturer/forms/ManufacturerForm.vue'

export interface IData {
  model: IManufacturerForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IManufacturerForm) => {
      return store.dispatch('manufacturer/createForm', dataForm)
    }) as ResponseFetching<Promise<IManufacturerForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    ManufacturerForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: 'New manufacturer',
        image: '',
        sortOrder: 0
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'manufacturer' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IManufacturerForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'manufacturer-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
